@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
.container-fluid {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 14px 28px; }

.logo {
  width: 200px;
  margin: 0; }

.main-menu {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  padding: 0; }
  .main-menu li {
    margin: 0 14px; }

.navbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.menu-box {
  display: flex;
  align-items: flex-end;
  flex-direction: column; }
  .menu-box p {
    padding-right: 12px;
    font-weight: 500; }
  .menu-box span {
    font-weight: 200;
    font-size: 14px; }

.page-title {
  margin: 14px 0;
  font-size: 56px;
  font-weight: 600;
  position: relative;
  color: #333; }

.small-title {
  margin: 7px 0;
  font-size: 35px;
  font-weight: 600;
  position: relative;
  color: #333; }

.button {
  background: transparent;
  border: 1px solid;
  color: #666;
  text-align: center;
  line-height: 1.3;
  padding: 7px 14px;
  margin: 14px 14px 14px 0;
  border-radius: 5px;
  cursor: pointer;
  opacity: 0.7;
  -webkit-transition: all 0.1s;
  transition: all 0.1s; }
  .button--success {
    color: #01916d; }
  .button--primary {
    color: #3498db; }
  .button--info {
    color: #2b7cb3; }
  .button--danger {
    color: #f0932b; }
  .button:hover {
    box-shadow: -5px -5px 20px rgba(0, 0, 0, 0.2);
    opacity: 1; }
  .button:active {
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.2); }

.progress-votes {
  cursor: progress; }

.disabled-votes {
  color: #666; }

.section-title {
  margin: 14px 0;
  font-weight: 600;
  position: relative;
  color: #333; }

.post-summary {
  border-bottom: 1px solid #ddd;
  padding: 14px;
  margin-bottom: 14px;
  margin-left: -14px;
  -webkit-transition: .2s;
  transition: .2s;
  cursor: pointer; }

.post-summary:hover {
  margin-left: -28px;
  margin-top: -2.8px;
  border-radius: 5px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  padding-left: 21px; }

.spinner-main {
  display: flex;
  justify-content: center; }

.spinner-box {
  position: relative;
  margin: 0 6px; }

.second-spinner {
  position: absolute;
  bottom: -5px;
  left: -8px; }

.alert-main {
  display: flex;
  justify-content: center; }

.alert {
  background: #fff;
  color: #333;
  border-left: 4px solid #111;
  padding: 0.9rem 0.8rem;
  margin: 1rem 0 1.5rem 0;
  display: flex;
  align-items: center;
  font-size: 1rem;
  opacity: 1;
  font-weight: lighter;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0.2);
  max-width: 350px;
  width: 100%;
  border-radius: 5px; }
  .alert__desc {
    margin-left: 10.76923px; }
  .alert--error {
    background: #ff5d6c;
    color: #fff;
    border-color: #EA2027; }
  .alert--warning {
    background: #d68529;
    color: #fff;
    border-color: #f0932b; }
  .alert--info {
    background: #3598db;
    color: #fff;
    border-color: #2b7cb3; }
  .alert--success {
    background: #00bc8c;
    color: #fff;
    border-color: #01916d; }

.pagination {
  justify-content: center;
  text-align: center; }
  .pagination__list {
    display: flex;
    justify-content: center;
    list-style-type: none;
    font-size: 21px; }
    .pagination__list__item {
      display: block;
      width: 28px;
      height: 28px;
      margin: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      border-radius: 5px;
      -webkit-transition: .2s;
      transition: .2s;
      color: #666; }
      .pagination__list__item--active {
        color: #3498db; }

.text-field {
  position: relative;
  display: block;
  margin: 28px 0 14px 0; }
  .text-field__input {
    border: none;
    border-bottom: 1px solid #666;
    font-family: inherit;
    outline: none;
    font-size: inherit;
    padding: 7px 0;
    -webkit-transition: .2s;
    transition: .2s;
    opacity: 0.8;
    background: none;
    width: 100%;
    max-width: 280px; }
    .text-field__input:hover {
      opacity: 1; }
    .text-field__input:focus {
      border-bottom: 2px solid #3498db;
      opacity: 1; }
  .text-field__label {
    position: absolute;
    top: -18.2px;
    left: 0;
    font-size: 14px;
    -webkit-transition: .2s;
    transition: .2s; }
    .text-field__label--big {
      font-size: inherit;
      top: 4px; }

.content-editor {
  outline: none;
  border: 4px dashed #ddd;
  max-width: 560px;
  padding: 14px;
  font-family: "Roboto", sans-serif;
  -webkit-transition: .2s;
  transition: .2s; }

.content-editor:hover {
  border: 4px dashed #ccc; }

.comments-main {
  display: flex;
  flex-direction: column;
  margin-top: 20px; }

.comments-info {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #666;
  font-family: "Roboto", sans-serif;
  font-size: 14px; }
  .comments-info p {
    margin: 0; }

.comments-edit {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%; }
  .comments-edit input {
    border: 1px solid #dcdde1;
    padding: 20px 10px;
    outline: none;
    width: 100%;
    margin: 10px 0; }

.add-button {
  height: 40px;
  margin-left: 20px; }

.comments-progress {
  cursor: progress; }

.comment-item p {
  margin: 0;
  padding: 5px; }

.comment-info {
  display: flex;
  justify-content: space-between; }
  .comment-info p {
    font-size: 12px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0; }
  .comment-info p:first-child {
    color: #2b7cb3;
    font-weight: 500; }
  .comment-info p:last-child {
    color: #00bc8c; }

.even-comment {
  background-color: #f5f6fa; }

.author-row {
  margin-bottom: 20px; }

