body {
  margin: 0;
  padding: 0;
  color: #666;
  background: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 18.2px;
  line-height: 1.7;
  letter-spacing: 0.5px;
  overflow-y: scroll; }

a, a:hover, a:visited {
  text-decoration: none; }

button {
  outline: none !important; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

a {
  color: #666;
  -webkit-transition: .3s;
  transition: .3s; }
  a:hover {
    color: #333; }
  a.active {
    color: #333;
    border-bottom: 1px solid #666; }

